<template>
  <div id="about">
    <v-expansion-panels v-model="panel1">
      <v-expansion-panel>
        <v-expansion-panel-header>
          <h3 align="center" justify="center">MISIÓN</h3>
        </v-expansion-panel-header>
        <v-expansion-panel-content>
          <v-row align="center" justify="center">
            <v-col cols="8" lg="8">
              <div class="text-justify">
                {{ mision }}
              </div>
            </v-col>
          </v-row>
        </v-expansion-panel-content>
      </v-expansion-panel>
    </v-expansion-panels>
    <v-expansion-panels v-model="panel2">
      <v-expansion-panel>
        <v-expansion-panel-header>
          <h3 align="center" justify="center">VISIÓN</h3>
        </v-expansion-panel-header>
        <v-expansion-panel-content>
          <v-row align="center" justify="center">
            <v-col cols="8" lg="8">
              <div class="text-justify">
                {{ vision }}
              </div>
            </v-col>
          </v-row>
        </v-expansion-panel-content>
      </v-expansion-panel>
    </v-expansion-panels>
  </div>
</template>
<script>
export default {
  name: "Home",

  components: {},
  data() {
    return {
      mision: "",
      vision: "",
      panel1: 0,
      panel2: 0,
      items: 1,
    };
  },
  mounted() {
    if (this.$route) this.route = this.$route.query.redirect;
    this.getMisionVision();
  },
  methods: {
    getMisionVision() {
      this.$http
        .get(`/listar-infoempresa`)
        .then((res) => {
          if (
            res.data.business[0].mision != null &&
            res.data.business[0].vision != null
          ) {
            this.mision = res.data.business[0].mision;
            this.vision = res.data.business[0].vision;
          }
        })
        .finally();
    },
  },
};
</script>

