<template>
  <div id="about">
    
      <v-card>
        <v-container
          class="mx-auto"
          max-width="100%"
          style="margin-top: 30px; margin-bottom: 40px"
          color="primary"
        >
          <v-row align="center" justify="center">
            <v-col
              cols="12"
              lg="6"
              md="6"
              sm="12"
              style="text-align: center; margin-right: -10%"
            >
              <v-avatar width="75%" height="75%">
                <img src="@/assets/images/valores.jpg" alt="John" />
              </v-avatar>
            </v-col>
            <v-col cols="12" lg="6" md="6" sm="12">
          <h3 align="center" justify="center">VALORES</h3>
          <br>
              <div style="text-align:justify">
                {{text}}
              </div>

            </v-col>
          </v-row>
        </v-container>
      </v-card>
  </div>
</template>
<script>
export default {
  components: {},
  name: "Home",
  data() {
    return {
      panel1: 0,
      panel2: 0,
      text:"",
      optvalues: [
      ],
      items: 1,
    };
  },
  mounted() {
    this.getValues();
    if (this.$route) this.route = this.$route.query.redirect;
  },

  methods: {
    getValues() {
      this.$http
        .get(`/listar-infoempresa`)
        .then((res) => {
          if (
            res.data.business[0].values.text != null &&
            res.data.business[0].values.optvalues != null
          ) {
            this.text = res.data.business[0].values.text;
            this.optvalues = res.data.business[0].values.optvalues;
          }
        })
        .finally();
    },
  },
};
</script>

